<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <el-row
        ><el-col
          ><el-button type="primary" @click="showAddCateDialog"
            ></el-button
          ></el-col
        ></el-row
      >

      <!--  selection-type -->
      <tree-table
        :data="catelist"
        :columns="columns"
        :selection-type="false"
        :expand-type="false"
        show-index
        index-text="#"
        border
        :show-row-hover="false"
        class="treeTable"
      >
        <!--  -->
        <template slot="isok" slot-scope="scope">
          <i
            class="el-icon-success"
            v-if="scope.row.cat_deleted === false"
            style="color: lightgreen"
          ></i>
          <i class="el-icon-error" style="color: lightgreen" v-else></i>
        </template>
        <!--  -->
        <template slot="order" slot-scope="scope">
          <el-tag size="mini" v-if="scope.row.cat_level === 0"></el-tag>
          <el-tag
            type="success"
            size="mini"
            v-else-if="scope.row.cat_level === 1"
            ></el-tag
          >
          <el-tag type="warning" size="mini" v-else></el-tag>
        </template>
        <!--  -->
        <template slot="opt" slot-scope="scope">
          <el-button type="primary" icon="el-icon-edit" size="mini"
            ></el-button
          >
          <el-button type="danger" icon="el-icon-delete" size="mini"
            ></el-button
          >
        </template>
      </tree-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[3, 5, 7, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addcateDialogVisible"
      width="50%"
      @close="addCateDialogClosed"
    >
      <!--  -->
      <el-form
        :model="addCateForm"
        :rules="addCateFormRules"
        ref="addCateFormRef"
        label-width="100px"
        class="demo-ruleForm"
      >
        <!-- options   props-->
        <el-form-item label="" prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-cascader
            expand-trigger="hover"
            v-model="selectedKeys"
            :options="parentCateList"
            :props="cascaderProps"
            @change="parentCateChanged"
            clearable
            change-on-select
          >
          </el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="addCate"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 3,
      },
      // 
      catelist: [],
      // 
      total: 0,
      // table
      columns: [
        {
          label: "",
          prop: "cat_name",
        },
        {
          label: "",
          //
          type: "template",
          template: "isok",
        },
        {
          label: "",
          //
          type: "template",
          template: "order",
        },
        {
          label: "",
          //
          type: "template",
          template: "opt",
        },
      ],
      // 
      addcateDialogVisible: false,
      addCateForm: {
          cat_name:'',
        //id
          cat_pid:0,
        //
          cat_level:0
      },
      //   
      addCateFormRules: {
        cat_name: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" },
        ],
      },
      // 
      parentCateList: [],
      //
      cascaderProps: {
        value: "cat_id",
        label: "cat_name",
        children: "children",
      },
      //id
      selectedKeys: [],
    };
  },
  created() {
    this.getCateList();
  },
  methods: {
    async getCateList() {
      const { data: res } = await this.$http.get("categories", {
        params: this.queryInfo,
      });
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      // catelist
      this.catelist = res.data.result;
      this.total = res.data.total;
    },
    // pagesize
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      this.getCateList();
    },
    //pagenum
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      this.getCateList();
    },
    // 
    showAddCateDialog() {
      // 
      this.getParentCateList();
      //   
      this.addcateDialogVisible = true;
    },
    // 
    async getParentCateList() {
      const { data: res } = await this.$http.get("categories", {
        params: { type: 2 },
      });
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      // console.log(res.data);
      this.parentCateList = res.data;
    },
    // 
    parentCateChanged() {
      // this.selectedKeys0 
      if (this.selectedKeys.length > 0) {
        //   id
        this.addCateForm.cat_pid = this.selectedKeys[
          this.selectedKeys.length - 1
        ];
        // 
        this.addCateForm.cat_level = this.selectedKeys.length;
        return;
      } else {
        //   
        this.addCateForm.cat_pid = 0;
        //   
        this.addCateForm.cat_level = 0;
      }
    },
    // 
    addCate() {
     this.$refs.addCateFormRef.validate(async valid=>{
         if(!valid)return
         const {data:res} = await this.$http.post('categories',this.addCateForm)
         if(res.meta.status!==201){
             return this.$message.error('')
         }
         this.$message.success('')
         this.getCateList()
         this.addcateDialogVisible= false
     })
    },
    // 
    addCateDialogClosed() {
      this.$refs.addCateFormRef.resetFields();
      this.addCateForm.cat_pid = 0;
      this.addCateForm.cat_level = 0;
    //   
     this.selectedKeys = "";
    },
  },
};
</script>

