<template>
  <div class="login_container">
    <div class="login_box">
      <!--  -->
      <div class="avatar_box">
        <img src="../assets/logo.png" alt="" />
      </div>
      <!--  -->
      <el-form class="login_form" :model="loginForm" :rules="loginFormRules" ref="loginFormRef">
        <!--  -->
        <el-form-item prop="username">
          <el-input
            placeholder=""
            prefix-icon="iconfont icon-user"
            v-model="loginForm.username"
            
          >
          </el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="password">
          <el-input
            placeholder=""
            prefix-icon="iconfont icon-lock_fill"
            v-model="loginForm.password"
            type="password"
          >
          </el-input>
        </el-form-item>
        <!--  -->
        <el-form-item class="btns">
          <el-button type="primary" @click="login"></el-button>
          <el-button type="info" @click="resetLoginForm"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      loginForm: {
        username: "admin",
        password: "123456",
      },
      //   
      loginFormRules: {
        //   
        username: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 15, message: " 3  15 ", trigger: "blur" },
        ],
        // 
        password: [
          { required: true, message: "", trigger: "blur" },
          { min: 6, max: 10, message: " 6  10 ", trigger: "blur" },
        ],
      },
    };
  },
  methods:{
    //   
      resetLoginForm(){
          this.$refs.loginFormRef.resetFields()
      },
    //   
      login(){
        //   
          this.$refs.loginFormRef.validate(async valid=>{
              if(!valid) return
              const{data:res} = await this.$http.post('login',this.loginForm)
              if(res.meta.status !== 200) return this.$message.error('');
             this.$message.success('')
            // 1.tokensessionStorage
               //1.1 API
               //1.2 tokentokensessionStorage
            window.sessionStorage.setItem('token',res.data.token)
            // 2. /home
            this.$router.push('/home')
          })
      }
  }
};
</script>

<style lang='less' scoped>
.login_container {
  background-color: #2b4b6b;
  height: 100%;
}
.login_box {
  width: 450px;
  height: 300px;
  background-color: #ffffff;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  .avatar_box {
    height: 130px;
    width: 130px;
    border: solid 1px #eeeeee;
    border-radius: 50%;
    padding: 10px;
    box-shadow: 0 0 10px #dddddd;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #ffffff;
    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: #eeeeee;
    }
  }
}

.login_form {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0px 20px;
  box-sizing: border-box;
}
.btns {
  display: flex;
  justify-content: flex-end;
}
</style>