<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <el-row
        ><el-col
          ><el-button type="primary" size="mini"></el-button></el-col
        ></el-row
      >
      <el-table :data="rolelist" border stripe>
        <!--  -->
        <el-table-column type="expand">
          <template slot-scope="scope" width="200px">
            <el-row
              :class="['bdbottom', i1 === 0 ? 'bdtop' : '', 'vcentre']"
              v-for="(item1, i1) in scope.row.children"
              :key="item1.id"
            >
              <!--  -->
              <el-col :span="5">
                <el-tag
                  type="primary"
                  closable
                  @close="removeRightById(scope.row, item1.id)"
                  >{{ item1.authName }}</el-tag
                >
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <el-row
                  :class="[i2 === 0 ? ' ' : 'bdtop', 'vcentre']"
                  v-for="(item2, i2) in item1.children"
                  :key="item2.id"
                >
                  <!--  -->
                  <el-col :span="6"
                    ><el-tag
                      type="warning"
                      closable
                      @close="removeRightById(scope.row, item2.id)"
                      >{{ item2.authName }}</el-tag
                    >
                    <i class="el-icon-caret-right"></i
                  ></el-col>
                  <!--  -->
                  <el-col :span="18"
                    ><el-tag
                      v-for="item3 in item2.children"
                      :key="item3.id"
                      type="success"
                      closable
                      @close="removeRightById(scope.row, item3.id)"
                      >{{ item3.authName }}</el-tag
                    ></el-col
                  >
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope" width="200px">
            <el-button type="primary" icon="el-icon-edit" size="mini"
              ></el-button
            >
            <el-button type="warning" icon="el-icon-delete" size="mini"
              ></el-button
            >
            <el-button
              type="danger"
              icon="el-icon-setting"
              size="mini"
              @click="showSetRightDialog(scope.row)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="setRightDialogVisible"
      width="30%"
      @close="setRightDialogClosed"
    >
      <!-- 
        
        default-checked-keys
         key 
       -->
      <el-tree
        :data="rightslist"
        :props="defaultProps"
        show-checkbox
        node-key="id"
        :default-expand-all="true"
        :default-checked-keys="defKeys"
        ref="treeRef"
      ></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightDialogVisible = false"> </el-button>
        <el-button type="primary" @click="allotRights"
          > </el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      rolelist: [],
      // 
      setRightDialogVisible: false,
      // 
      rightslist: [],
      // 
      defaultProps: {
        children: "children",
        label: "authName",
      },
      // id
      defKeys: [],
      // id
      roleId:''
    };
  },
  created() {
    this.getRoleList();
  },
  methods: {
    async getRoleList() {
      const { data: res } = await this.$http.get("roles");
      if (res.meta.status !== 200) return this.$message.error("");
      this.rolelist = res.data;
      // console.log(this.rolelist);
    },
    // id
    async removeRightById(role, rightId) {
      const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => err);
      if (confirmResult !== "confirm") return this.$message.info("");
      console.log("");
      const { data: res } = await this.$http.delete(
        `roles/${role.id}/rights/${rightId}`
      );
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      // this.getRoleList()
      //  
      role.children = res.data;
    },
    // 
    async showSetRightDialog(role) {
      this.roleId=role.id
      const { data: res } = await this.$http.get("rights/tree");
      if (res.meta.status !== 200)
        return this.$message.error("");
      // rightlist
      this.rightslist = res.data;
      // console.log(this.rightslist);
      // 
      this.getLeafKeys(role, this.defKeys);
      this.setRightDialogVisible = true;
    },
    // id
    getLeafKeys(node, arr) {
      // nodechildren
      if (!node.children) return arr.push(node.id);
      node.children.forEach((item) => {
        this.getLeafKeys(item, arr);
      });
    },
    //
    setRightDialogClosed() {
      this.defKeys = [];
    },
    // 
   async allotRights(){
      const keys = [
        // 
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      const idStr = keys.join(',')
      //rids id 
     const{data:res}=await this.$http.post(`roles/${this.roleId}/rights`, {rids:idStr})
     if(res.meta.status!==200)return this.$message.error('')
     this.$message.success('')
     this.getRoleList()
     this.setRightDialogVisible=false
    }
  },
};
</script>

<style>
.el-tag {
  margin: 7px;
}
.bdtop {
  border-top: 1px solid #eeeeee;
}
.bdbottom {
  border-top: 1px solid #eeeeee;
}
.vcentre {
  display: flex;
  align-items: center;
}
</style>